.wrapper{
    background-color: rgba(19, 19, 19, 0.745);
    position: relative;
    display: inline-table;
    min-width: 250px;
    margin: 0 auto;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.wrapper img{
    display: block;
    width: 100%;
}

.container{
    padding: 30px 20px;
}
.container h2{
    text-align: left;
    font-size: 14px;
    color: #d5d5d5;
    line-height: 1.5;
}
.container h1{
    text-align: left;
    color: aquamarine;
    font-weight: 900;
}

.description{
    margin-top: 40px;
}
.description p{
    font-size: 14px;
    line-height: 1.5;
}

.container ul{
    margin: 30px 0 0 0;
    list-style-image: url('./Marker.png');
    padding-left: 20px;
}
.container li{
    line-height: 1.5;
}



.info{
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
    padding: 30px 20px;
}
.info p{
    margin: 0;
}
.info .available p:last-child, .info .duration p:last-child{
    color: #d5d5d5;
    line-height: 2;
}
.info .available p:first-child, .info .duration p:first-child{
    text-transform: uppercase;
    font-weight: 700;
}
.info .available p:first-child, .info .duration p:first-child span:last-child{
    font-size: 20px;
}
.duration span:first-child{
    font-size: 40px;
    line-height: 0.8;
}
.info .available p:first-child{
    padding-top: 11px;
}
.info .available p:first-child, .info .duration span{
    color: aquamarine;
    font-weight: 900;
}



.wrapper button{
    background: linear-gradient(#CA0AA5, #5a1c48);
    color: #fff;
    width: 100%;
    font-size: 16px;
    font-weight: 700;
    padding: 15px 20px; 
    text-transform: uppercase;
    border: none;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.wrapper button:after{
    content: "";
    height: 70%;
    left: -200%;
    opacity: 0;
    position: absolute;
    top: -20%;
    transform: rotate(-50deg);
    width: 350%;
    background: rgba(255, 255, 255, 0.13);
    background: linear-gradient(to right, rgba(255, 255, 255, 0.244)0%, rgba(255, 255, 255, 0.13) 77%, rgba(255, 255, 255, 0.5) 92%, rgba(255, 255, 255, 0.0) 100%);
}
.wrapper button:hover:after{
    left: -50%;
    opacity: 1;
    top: -50%;
    transition-duration: .7s, 0.3s, 0.15s;
    transition-property: left, top, opacity;
    transition-timing-function: ease;
}
.wrapper button:active:after{
    opacity: 0;
}
