.intro{
    overflow: hidden;
    background-color: #000000;
}

.fullScreen{
    position: relative;
    height: 100vh;    
    transition: .3s ease-out;
    opacity: 1;
}

.sliderBtn{
    position: relative;
    position: absolute;
    height: 100px;
    width: 10%;
    max-width: 70px;
    top: 0;
    bottom: 0;
    margin: auto 0;
    z-index: 2;
    align-items: center;
    transition: .2;
}
.sliderBtn:hover{
    background-color: #8989891c;
}

.sliderBtn img{
    width: 40px;
    height: 40px;
    filter: invert(100%);
    opacity: 50%;
    position: absolute;
    margin: auto;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    left: 0;
    margin: 0 auto;
}

.sliderBtnRight{
    right: 0;
}


.sliderBtnDone{
    display: none;
}

.fullScreenIntroWrapper{
    position: relative;
    height: 100%;
    background-color: #000000;
}

.fullScreenIntro{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: 1s;
    opacity: 0;
}

.fullScreenIntroDone{
    opacity: 1;
}

.slider{
    display: flex;
    position: absolute;
    top: 50%;
    transform: translateY(-60%);
    overflow: hidden;
    transition: .8s;
}
.sliderHide{
    display: none;
}

.slide{
    width: 100vw;
    text-align: center;
}

.toSlideVideoDone{
    transform: translate(0%, -60%);
}
.toSlideAboutDone{
    transform: translate(-100vw, -60%);
}
.toSlideSocialsDone{
    transform: translate(-200vw, -60%);
}

.introSlideVideo, .introSlideAbout, .introSlideSocials{
    padding: 0 50px;
}

.introSlideVideo{
    transform: translateY(20%);
    opacity: 0;
    transition: 1s;
}
.introSlideVideoEnterDone{
    opacity: 1;
    transform: translateY(0%);
}

.introSlideVideo .introSlideContainer h2{
    font-style: italic;
}

.introSlideVideo img{
    max-width: 550px;
    width: 100%;
}

.introSlideVideo hr{
    min-width: 40px;
    max-width: 60px;
    width: 7%;
    height: 2px;
    background-color: #fff;
    margin: 4vh auto;
    border: none;
}

.introSlideVideo h2{
    font-size: 14px;
    font-style: italic;
}

.introSlideAbout img{
    max-width: 150px;
    max-height: 150px;
    width: 30vw;
    height: 30vw;
    border-radius: 100%;
}

.introSlideAbout h2{
    font-size: 14px;
    margin: 15px auto 0 auto;
    max-width: 400px;
}

.introSlideSocials img{
    width: 50px;
    height: 50px;
    margin: 10px;
    transition: .2s;
    cursor: pointer;
}
.introSlideSocials img:hover{
    scale: .9;
    opacity: .8;
}
.introSlideSocials a{
    cursor: auto;
}

.introSlideSocials h2{
    font-size: 0px;
    
}

.introBtns{
    position: absolute;
    top: 70%;
    right: 0;
    left: 0;
    margin: 0 auto;
    text-align: center;
    padding: 0px 20px;
}

.introBtns a{
    width: 200px;
    height: 42px;
    display: inline-block;
    padding: 10px 30px;
    font-size: 16px;
    font-weight: 700;
    text-decoration: none;
    border-radius: 24px;
    margin: 0 10px 15px 10px;
    position: relative;
    overflow: hidden;
}

.introBtnWatchShowReel{
    background-image: linear-gradient(to bottom right,var(--tw-gradient-stops));
    --tw-gradient-from: #DA206A;
    --tw-gradient-stops: var(--tw-gradient-from),var(--tw-gradient-to, rgb(218 32 106 / 0));
    --tw-gradient-to: #EE5C9C;
    color:#FFFFFF;
    transform: translateX(-10%);
    cursor: none;
    transition: 1s;
    opacity: 0;
}

.introBtnWatchShowReelEnterActiveVisibility{
    transform: translateX(0%);
    opacity: 1;
}
.introBtnWatchShowReelEnterDoneVisibility{
    transform: translateX(0%);
    opacity: 1;
    transition: .3s;
}

.introBtnContact{
    color: #1d1d1d;
    background: #FEA801;
    opacity: 0;
    transform: translateX(-30%);
    transition: 1s;
}
.contactBtnEnterDone{
    background-color: #fff;
}
.introBtnContactEnterActiveVisibility{
    transform: translateX(0%);
    opacity: 1;
}
.introBtnContactEnterDoneVisibility{
    transform: translateX(0%);
    opacity: 1;
    transition: .3s;
}

.introBtnWatchShowReel:after, .introBtnContact::after{
    content: "";
    height: 50%;
    left: -200%;
    opacity: 0;
    position: absolute;
    top: -20%;
    transform: rotate(-50deg);
    width: 350%;
    background: rgba(255, 255, 255, 0.13);
    background: linear-gradient(to right, rgba(255, 255, 255, 0.43) 0%, rgba(255, 255, 255, 0.13) 77%, rgba(255, 255, 255, 0.5) 92%, rgba(255, 255, 255, 0.0) 100%);
}
.introBtnWatchShowReel:hover:after, .introBtnContact:hover:after{
    left: -50%;
    opacity: 1;
    top: -50%;
    transition-duration: 1s, 0.7s, 0.15s;
    transition-property: left, top, opacity;
    transition-timing-function: ease;
}
.introBtnWatchShowReel:active:after, .introBtnContact:active:after {
    opacity: 0;
}



.contentController{
    position: absolute;
    display: flex;
    bottom: 0;
    right: 50%;
    transform: translateX(50%);
    width: 100%;
    display: flex;
}

.contentControllerBtn{
    width: 100%;
    line-height: 40px;
    padding: 0px 100px;
    font-size: 20px;
    font-weight: 900;
    border: none;
    cursor: pointer;
    color: #8C8C8C;
    background: linear-gradient(270deg, rgba(21, 21, 21, 0) -11.55%, rgba(20, 20, 20, 0.56) 99.38%);
    backdrop-filter: blur(7.5px);
    font-family: 'Roboto';
}

.contentControllerPortfolioDone{
    background: linear-gradient(270deg, #FA00CE -11.55%, rgba(21, 21, 21, 0) 81.3%);
    color: #fff;
}
.contentControllerEducationDone{
    background: linear-gradient(-270deg, #FA00CE -11.55%, rgba(21, 21, 21, 0) 81.3%);
    color: #fff;
}

.contentController button:first-child{
    text-align: end;
}
.contentController button:last-child{
    text-align: start;
}



.overlay iframe{
    width: 100vw;
    height: 100vh;
    border: none;
}

.overlayCloseBtn{
    position: absolute;
    top: 0;
    width: 40px;
    height: 40px;
    margin: 110px 0 0 50px;
    border-radius: 100%;
    cursor: pointer;
}
.overlayCloseBtn:before {
    content: '+';
    color: #fff;
    position: absolute;
    z-index: 2;
    transform: rotate(45deg);
    font-size: 50px;
    line-height: 1;
    top: -5px;
    left: 6px;
    transition: all 0.3s cubic-bezier(0.77, 0, 0.2, 0.85);
}
.overlayCloseBtn:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    background: rgb(250, 0, 206, .8);
    z-index: 1;
    transition: all .2s cubic-bezier(0.77, 0, 0.2, 0.85);
    transform: scale(0.01);
}
.overlayCloseBtn:hover:after {
    transform: scale(1);
}
.overlayCloseBtn:hover:before {
    transform: scale(0.8) rotate(45deg);
    color: #fff;
}






@media screen and (min-height: 750px){
    .introSlideSocials h2{
        font-size: 14px;
        margin-top: 10px;
    }
}
@media screen and (min-width: 450px){
    .introSlideVideo h2, .introSlideSocials h2{
        font-size: 16px;
    }
}
@media screen and (min-width: 520px){
    .introSlideSocials h2{
        margin-top: 20px;
    }
    .introSlideSocials div{
        margin-top: 50px;
    }
}
@media screen and (min-width: 620px){
    .introSlideSocials h2, .introSlideVideo h2, .introSlideAbout h2{
        font-size: 18px;
    }
    .introSlideAbout h2{
        margin-top: 30px;
    }
}
@media screen and (min-width: 1200px){
    .introBtns a{
        font-size: 18px;
        width: 220px;
    }
}
@media screen and (max-height: 650px){
    .introSlideVideo img, .introSlideAbout img{
        height: 20vh;
    }
    .introSlideAbout img{
        width: 20vh;
    }
    .introSlideSocials h2, .introSlideVideo h2, .introSlideAbout h2{
        font-size: 14px;
    }
    .introSlideAbout h2{
        margin-top: 10px;
    }
}
@media screen and (max-height: 600px){
    .introSlideVideo img, .introSlideAbout img{
        height: 20vh;
    }
    .introSlideSocials h2{
        font-size: 0px;
    }
    .introBtns a{
        font-size: 14px;
        width: 180px;
        height: 37px;
    }
    .contentController button:first-child, .contentController button:last-child{
        line-height: 35px;
        font-size: 18px;
    }
}

@media screen and (max-width: 700px){
    .contentController button:first-child, .contentController button:last-child{
        padding: 0;
        text-align: center;
    }
}

@media screen and ((max-width: 475px) or (max-height: 600px)){
    .overlayCloseBtn{
        margin: 80px 0 0 20px;
    }
}






