.footer{
    width: 100%;
    background-color: #000000;
    color: #FFFFFF;
}

.footer_container{
    max-width: 1860px;
    width: 100%;
    padding: 50px;
    margin: 0 auto;
}

.footer_grid{
    display: grid;
    column-gap: 30px;
    row-gap: 55px;
    grid-template-columns: repeat(3,minmax(0,1fr));
}

.footer_column{
    margin: auto 0;
}

.footer_column:first-child{
    gap: 30px;
    display: flex;
    justify-content: left;
    flex-wrap: wrap;
}

.footer_column:last-child .grid_item{
    text-align: end;
}

.footer_column:last-child .grid_item span{
    font-size: 12px;
}

.grid_item{
    text-align: center;
}

ul{
    padding: 0;
}
li{
    list-style-type: none;
}

ul:first-child img{
    height: 40px;
    width: 40px;
    transition: .3s;
}
ul:first-child img:hover{
    transform: scale(.9);
}

.grid_item::before{
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
}

.footer_icon{
    height: 35px;
    transition: .3s ease-out;
}

.footer_icon:hover{
    height: 45px;
    opacity: .6;
}

.footer_logo{
    width: 200px;
    cursor: pointer;
}


@media screen and (max-width: 1333px) {
    .footer_grid{
        grid-template-columns: repeat(1,minmax(0,1fr));
    }
    .footer_column:last-child .grid_item{
        text-align: center;
    }
    .footer_column:first-child{
        justify-content: center;
    }
}